
main.landing-page {
    -ms-flex: 1 0 auto;
        flex: 1 0 auto;
    background-image: url('../../assets/landingPageBackground.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 600px;
}


.aad-title {
    font-size: 5em;
    font-weight:700;
    text-shadow: 3px 3px #000;;
    color: white;
    margin-left: 1em;
    line-height: 70px;
}


#aad-paragraph h5 {
    font-weight:700;
    text-shadow: 3px 3px #000;;
    color: white;
    margin-left: 6em;
    margin-top: 3em;
    line-height: 20px;
    max-width: 70%;
    text-align: center;
}

#signup-login-buttons-container {
    text-align:center;
    margin-top: 4em;
}

.signup-login-buttons {
    margin: 1em;
    background-color: white;
    color: black;
    font-size:1.4em;
}

.signup-login-buttons:hover,
.signup-login-buttons:focus {
    background-color: #033076;
    color: white;
}


