.reviews-wrapper {
    max-width: 70%;
    margin:auto;
}

.materialize-textarea {
    min-width: 225% !important;
}

.c-item {
    min-width: 100%;
}

#delete-button {
    background-color: #b71c1c;
    color: white;
    -webkit-transition: .5s !important;
    -o-transition: .5s !important;
    transition: .5s !important;
    min-width: 50%;
}

#delete-button:hover {
    border: 1px solid #033076;
}

#delete-button:focus {
    background-color: #033076;
}


